import React from "react";
import { Grid, makeStyles } from "@material-ui/core";
import Item from "../models/Item";
import InventoryGridItem from "./InventoryGridItem";

const useStyles = makeStyles({
    main: {
        textAlign: "center",
        width: "100%",
    },
    container: {
        position: "relative",
        height: "180px",
        width: "400px",
        margin: "auto",
    },
    gridItem: {
        height: "85px",
    },
    inventory: {
        padding: "10px 30px",
        position: "absolute",
    },
    item: {
        width: "100%",
        border: "solid",
        borderRadius: "15%",
        borderWidth: "3px",
        borderColor: "lightgrey",
        cursor: "pointer",
        backgroundColor: "whitesmoke",
    },
    itemSelected: {
        width: "100%",
        border: "solid",
        borderRadius: "15%",
        borderWidth: "3px",
        borderColor: "cadetblue",
        cursor: "pointer",
        backgroundColor: "powderblue",
    },
});

type InventorySelectorProps = {
    selected: boolean[];
    onSelectionChanged?: (itemId: string) => void;
    multiSelect?: boolean;
    items: Item[];
};

const InventorySelector: React.FunctionComponent<InventorySelectorProps> = (props: InventorySelectorProps) => {
    const classes = useStyles();
    const { selected, onSelectionChanged, items, multiSelect } = props;

    console.log(selected);

    const renderInventory = () => {
        let index = 0;
        const maxItems = 8;

        const itemGrids = items.map((item) => {
            const output = (
                <InventoryGridItem
                    item={item}
                    index={index}
                    selected={selected[index]}
                    onSelectionChanged={(id: string) => (onSelectionChanged ? onSelectionChanged(item.id) : undefined)}
                />
            );
            index++;
            return output;
        });

        const emptyItems = [];
        for (let i = items.length; i < maxItems; i++) {
            emptyItems.push("");
        }

        const emptyGrids = emptyItems.map((i) => {
            const output = <InventoryGridItem index={index} selected={false} />;
            index++;
            return output;
        });

        return itemGrids.concat(emptyGrids);
    };

    return (
        <div className={classes.main}>
            <div className={classes.container}>
                <div className={classes.inventory}>
                    <Grid container spacing={3} alignItems="center" justify="space-evenly">
                        {renderInventory()}
                    </Grid>
                </div>
            </div>
        </div>
    );
};

export default InventorySelector;
