import React from "react";
import { Grid, Tooltip, makeStyles } from "@material-ui/core";
import Item from "../models/Item";
import ItemTooltipContent from "./ItemTooltipContent";

const useStyles = makeStyles({
    gridItem: {
        height: "85px",
    },
    item: {
        width: "100%",
        border: "solid",
        borderRadius: "15%",
        borderWidth: "3px",
        borderColor: "lightgrey",
        cursor: "pointer",
        backgroundColor: "whitesmoke",
    },
    itemSelected: {
        width: "100%",
        border: "solid",
        borderRadius: "15%",
        borderWidth: "3px",
        borderColor: "cadetblue",
        cursor: "pointer",
        backgroundColor: "powderblue",
    },
});

type InventoryGridItemProps = {
    item?: Item;
    index: number;
    selected: boolean;
    onSelectionChanged?: (itemId: string) => void;
};

const InventoryGridItem: React.FunctionComponent<InventoryGridItemProps> = (props: InventoryGridItemProps) => {
    const classes = useStyles();
    const { item, index, selected, onSelectionChanged } = props;

    const imgClass = selected ? `${classes.itemSelected}` : `${classes.item}`;

    if (item === undefined) {
        return (
            <Grid item xs={3} className={classes.gridItem} key={index.toString()}>
                <Tooltip title={"Nothing"} key={index}>
                    <img src={"/images/items/empty.png"} id={index.toString()} className={imgClass} alt="nothing" />
                </Tooltip>
            </Grid>
        );
    }

    return (
        <Grid item xs={3} className={classes.gridItem} key={item.id}>
            <Tooltip title={<ItemTooltipContent item={item} />} key={index} arrow>
                <img
                    src={item.imageUrl}
                    id={index.toString()}
                    onClick={() => (onSelectionChanged ? onSelectionChanged(item.id) : null)}
                    className={imgClass}
                    alt={item.name}
                />
            </Tooltip>
        </Grid>
    );
};

export default InventoryGridItem;
