import React from "react";
import { Button, Grid, makeStyles, Tooltip } from "@material-ui/core";

import IAttackResult from "../../models/AttackResult";
import MockPartyService from "../../services/MockPartyService";
import MockHeroService from "../../services/MockHeroService";
import Hero from "../../models/character/Hero";

const useStyles = makeStyles({
    container: {
        position: "relative",
        height: "250px",
    },
    hero: {
        width: "70px",
        height: "70px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "lightgrey",
        margin: "4px",
        cursor: "pointer",
    },
});

type AttackResolveProps = {
    /** The target of the attack */
    target: string;

    /** The attack number */
    attackModifier: number;

    /** The attack table to use */
    attackTable: string;

    /** A valid selection has been made */
    onReady: () => void;
};

const AttackResolve: React.FunctionComponent<AttackResolveProps> = (props: AttackResolveProps): JSX.Element | null => {
    const classes = useStyles();

    const [heroes, setHeroes] = React.useState<Hero[] | null>(null); // The map graph data
    const [attackResult, setAttackResult] = React.useState<IAttackResult | undefined>(undefined);

    React.useEffect(() => {
        // Initial load of heroes
        const determineAttackResult = () => {
            // // TODO: Try / catch
            const heroService = new MockHeroService();
            const partyService = new MockPartyService();

            const newParty = partyService.getParty();
            if (!newParty) {
                console.error(`No party data`);
                return;
            }

            setHeroes(newParty);
            
            // const newStashItems = itemService.getItems(props.itemIds);
            // setLootItems(newStashItems);
        };

        if (attackResult === null || heroes === null) {
            determineAttackResult();
        }
    });

    if (!attackResult) return null;

    const renderParty = () => {
        if (!heroes) return null;

        let index = 0;

        return heroes.map((hero) => {
            const output = (
                <Grid item xs={3} style={{ textAlign: "center" }}>
                    <Tooltip title={hero.name} key={index}>
                        <img src={hero.imageUrl} id={index.toString()} className={classes.hero} alt="profile" />
                    </Tooltip>
                </Grid>
            );
            index++;
            return output;
        });
    };

    return (
        <div className={classes.container}>
            Resolve
            <Grid container>{renderParty()}</Grid>
        </div>
    );
};

export default AttackResolve;
