import React from "react";
import { Button, Grid, makeStyles, Tooltip } from "@material-ui/core";

import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import Item from "../../models/Item";
import InventoryGridItem from "../InventoryGridItem";
import MockItemService from "../../services/MockItemService";
import MockPartyService from "../../services/MockPartyService";

const useStyles = makeStyles({
    main: {
        textAlign: "center",
        width: "100%",
    },
    container: {
        position: "relative",
        height: "130px",
        width: "400px",
        margin: "auto",
    },
    inventory: {
        padding: "10px 30px",
    },
    itemsButton: {
        margin: "0 10px",
    },
    itemsButtonDiv: {
        width: "100%",
        textAlign: "center",
        paddingBottom: "10px",
    },
});

type ItemUseProps = {
    /** The item selection list */
    itemIds: string[];

    onReady: () => void;
};

const ItemUse: React.FunctionComponent<ItemUseProps> = (props: ItemUseProps): JSX.Element | null => {
    const classes = useStyles();

    const { itemIds, onReady } = props;

    const [items, setItems] = React.useState<Item[] | undefined>(undefined);
    const [selected, setSelected] = React.useState<boolean[]>([]);
    const [used, setUsed] = React.useState<boolean>(false);

    React.useEffect(() => {
        // Initial load of inventory
        const loadItems = () => {
            // TODO: Try / catch
            const itemService = new MockItemService();
            const newItems = itemService.getItems(itemIds);

            setItems(newItems);
        };

        if (items === undefined) {
            loadItems();
        }
    });

    if (!items) return null;

    const selectionChanged = (id: string) => {
        if (used) return;
        
        const match = items.find((i) => i.id === id);
        if (match === undefined) return;
        const index = items.indexOf(match);
        if (index < 0) return;

        const newSelected = selected.splice(0);
        newSelected[index] = !newSelected[index];
        setSelected(newSelected);
    };

    const selectedCount = selected.filter((i) => i === true).length;
    const isSelected = selectedCount > 0;

    const selectedIndex = selected.indexOf(true);
    const selectedItem = items[selectedIndex];

    const useItem = () => {
        if (!isSelected) return;
        //if (onEquipItem) onEquipItem(selectedItem.id);

        const partyService = new MockPartyService();

        // Remove the item!
        partyService.removeInventoryItem(selectedItem.id);
        onReady();
        setUsed(true);
    };

    const inventoryToolTip = () => {
        if (!isSelected) return "Please select an inventory item";

        return "Use the item";
    };

    let index = 0;

    const itemGrids = items.map((item) => {
        const output = (
            <InventoryGridItem
                item={item}
                index={index}
                selected={selected[index]}
                onSelectionChanged={(id: string) => selectionChanged(item.id)}
            />
        );
        index++;
        return output;
    });

    return (
        <div>
            <div className={classes.main}>
                <div className={classes.container}>
                    <div className={classes.inventory}>
                        <Grid container spacing={3} alignItems="center" justify="space-evenly">
                            {itemGrids}
                        </Grid>
                    </div>
                </div>
            </div>
            <div className={classes.itemsButtonDiv}>
                <Tooltip title={inventoryToolTip()}>
                    <span>
                        <Button
                            className={classes.itemsButton}
                            onClick={useItem}
                            disabled={!isSelected || used}
                            variant="contained"
                            color="primary"
                            startIcon={<ArrowUpwardIcon />}
                        >
                            {used ? "Used" : "Use"}
                        </Button>
                    </span>
                </Tooltip>
            </div>
        </div>
    );
};

export default ItemUse;
