import Item from "../models/Item";

import WolfSpear from "./items/wolf_spear.json";
import TokenOfServitude from "./items/token_of_servitude.json";
import HagTeeth from "./items/hag_teeth.json";
import CeramicHelm from "./items/ceramic_helm.json";
import CeramicArmor from "./items/ceramic_armor.json";

import Rope from "./items/rope.json";
import Torch from "./items/torch.json";
import Shovel from "./items/shovel.json";

export default class MockItemService {
    getItemDetail(id: string): Item {
        switch (id) {
            case "token_of_servitude":
                return TokenOfServitude as Item;
            case "hag_teeth":
                return HagTeeth as Item;
            case "ceramic_helm":
                return CeramicHelm as Item;
            case "ceramic_armor":
                return CeramicArmor as Item;
            case "torch":
                return Torch as Item;
            case "shovel":
                return Shovel as Item;
            case "rope":
                return Rope as Item;
            case "wolf_spear":
                return WolfSpear as Item;
            default:
                return WolfSpear as Item;
        }
    }

    getItems = (ids: string[]): Item[] => ids.map(id => this.getItemDetail(id));
}
